.core-layout__page {
  display: flex;
  flex: 1 1 0%;
  height: 100%;
  max-height: 100%; }

.core-layout__topbar {
  background-color: #2B4454;
  height: 60px; }
  .core-layout__topbar .top-bar {
    display: flex;
    height: 60px; }
  .core-layout__topbar .top-bar__left {
    display: flex;
    flex-basis: 100%;
    align-items: center; }
    @media (min-width: 768px) {
