        justify-content: space-between;
        min-width: 250px;
        max-width: 250px; } }
  .core-layout__topbar .top-bar__right {
    display: flex;
    flex-basis: 0;
    align-items: center;
    justify-content: space-between; }
    .core-layout__topbar .top-bar__right .top-bar__actions-left,
    .core-layout__topbar .top-bar__right .top-bar__actions-right {
      align-items: center; }
    .core-layout__topbar .top-bar__right .top-bar__actions-left {
      display: none; }
