        .core-layout__topbar .top-bar__right .top-bar__actions-left {
          display: flex; } }

.core-layout__sidebar-wrapper {
  position: fixed;
  flex: 1 1 0%;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  opacity: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.5); }
  @media (min-width: 768px) {
    .core-layout__sidebar-wrapper {
      display: block;
      position: static;
      width: 250px;
      min-width: 250px;
      max-width: 250px;
      opacity: 1; } }

.core-layout__sidebar {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  right: auto;
  bottom: 0;
  left: 0;
  width: 250px;
  min-width: 250px;
  max-width: 250px;
  overflow-y: auto;
  transform: translate3d(-100%, 0, 0); }
  @media (min-width: 768px) {
